@charset "UTF-8";

$colour-primary: #996699;
$colour-secondary: #003399;
$colour-accent: $colour-secondary;
$colour-info: rgb(159,224,244);

$pat-toolbar-line-height: 39px;

// @import url(http://fonts.googleapis.com/css?family=Open+Sans:400italic,400,300,700&subset=latin,greek,cyrillic-ext,cyrillic,greek-ext,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400italic,400,300,700&subset=latin,cyrillic-ext,cyrillic,greek-ext,greek,latin-ext);
// @import url(http://fonts.googleapis.com/css?family=Lato:300,400,900,400italic,900italic);

body {

	.pat-back-button {
		line-height: 39px;
	}

	.tool-tile {
		.tool-info .tool-title {
			font-stretch: condensed;
			font-size: 34px;
			margin-bottom: 5px;
		}
	}

	.pat-rich {
		h1 {
			font-family: Myriad Pro;
			font-stretch: condensed;
			margin-bottom: 30px;
			font-size: 2.5em;
		}

		.summary {
			font-weight: 300;
			font-size: 1.3em;
			// line-height: 1.2;
		}
	}

	.pat-message,
	.pat-well {
		h1 {
			font-family: Myriad Pro;
			font-stretch: condensed;
			margin-bottom: 0;
			font-size: 1.5em;
		}
	}

	.pat-button {
		height: 34px;
		line-height: 37px;
	}

	.tag-cloud {
		.tag {
			line-height: 24px;
			height: 22px;
		}
	}

	#screen-intro {
		.variation-dashboard & {
			h1 {
				background-image: url(../../oira/style/oira-logo-colour.svg);
				background-repeat: no-repeat;
				background-size: contain;
				background-position: top left;
				color: transparent;
				width: 170px;
				height: 80px;
				margin-bottom: 40px;
			}
		}
	}

	#content {
		#main-content {
			&.variation-dashboard {
				#content-pane {
					@media screen and (min-width: 768px) {
						// background: rgba(0, 51, 153, 0.14) url(../../oira/style/stars.svg) no-repeat;
						// background-size: 100% auto;
					}
				}
			}
		}
	}

	
}