$button-spacing: 0.8em;

.button-bar {
	margin-bottom: 1em;
	overflow-y: hidden;

	.pat-button {
		vertical-align: middle;
		margin-right: $button-spacing;
		margin-bottom: $button-spacing;
		float: left;
		
		&.float-after {
			float: right;
			margin-right: 0;
			margin-left: $button-spacing;
		}
	}

	&#nav-bar {

		#main-content > &,
		#main-content > form > & {
			.pat-button.continue,
			.pat-button.skip,
			.pat-button.float-after {
				float: right;
				margin-right: 0;
				margin-left: $button-spacing;

			}
		}

		.pat-button.skip,
		.pat-button.back,
		.pat-button.back:before {
			// background: $button-background-colour;
			color: $button-text-colour;
		}

		.pat-button.back:hover:before,
		.pat-button.skip:hover {
			color: $button-text-colour-hover;
		}
		
		@include screen-size(small) {
			position: fixed;
			bottom: 0;
			margin: 0;
			left: 4%;
			right: 4%;
			background: white;
			padding: 15px 0 0.5em;
			@include clear;
			z-index: 10;
			border-top: 1px solid black;
			transition: transform 1s;
			font-size: 120%;

			.focus-sidebar & {
				transform: translateY(100%);
			}

			.pat-button {
				padding: 0 1em;
				font-size: 0.7em;
				height: 35px;
				line-height: 36px;
				position: relative;
				max-width: 100%;
				margin-bottom: 0.5em;
			}
		}
		@include screen-size(medium-large) {
			position: absolute !important;
			top: auto !important;
			bottom: 0 !important;
			margin: 0 1rem; // Margin left and right required to not cover scrollbar in IE.
			left: 0;
			right: 0;
			z-index: 10;
			padding: 1em 1rem calc(1em - #{$button-spacing});
			// background-color: white;
			background: $colour-base;

			&:after {
				position: absolute;
				right: 15px;
				left: 15px;
				top: 0;
				height: 1px;
				content: ' ';
				display: block;
				z-index: -1;
				pointer-events: none;
				background-color: $colour-separation-lines;
			}

			#content-pane > & {
				position: sticky !important;
				margin: 0;
				padding: 1em 0 calc(1em - #{$button-spacing});

				&.bumped {
					&:after {
						display: block;
					}
				}

				&:after {
					left: 0;
					right: 0;
					display: none;
				}
			}
		}

		.pat-button {

			@include screen-size(medium-large) {
				line-height: 51px;
				height: 49px;
				padding: 0 45px;
			}

			&.back {
				width: 35px;
				color: transparent;
				text-align: center;
				overflow: hidden;
				padding: 0;
				position: relative;

				&:before {
					font-family: fontello;
					width: 100%;
					height: 100%;
					position: absolute;
					top: 0;
					left: 0;
					content: '#{$glyph-left}';
					font-size: 28px;
					@include screen-size(medium-large) {
						line-height: 48px;
					}
				}
			}

			&.continue {
				background-color: $colour-button-continue;
				color: $colour-button-continue-text;
			}
		}
	}
}

.pat-checklist.pat-clone + .button-bar.cloning {
	transition: padding-left 0.4s;
	margin-top: -0.8em;
}

.pat-checklist.pat-clone.has-value + .button-bar.cloning {
	padding-left: 1.5em;
	margin-top: -0.8em;
}