$tool-tile-padding: 22px;
$bullet-space: 26px;

// Menu list

$menu-list-top-bottom-padding: 4px !default;

// OiRA Colours
$colour-oira-cyan: #10B2E4;
$colour-oira-purple-xl: #dfd8e4;
$colour-oira-cyan-xl: #e7f2fb;

// Primary colours
$colour-primary: #996699 !default;
$colour-secondary: #003399 !default;
$colour-base: $colour-oira-cyan-xl !default;
$colour-alert: red !default;
$colour-accent: #0081be !default;
$colour-success: #acc700 !default;
$colour-warning: #f6a400 !default;
$colour-info: #10b2e4 !default;
$colour-notice: #fceea9 !default;
$colour-tag: #b4a4bf !default;
$colour-link: $colour-secondary;

$colour-base-font: #222222 !default;
$colour-sidebar: $colour-base !default;
$colour-sidebar-text: #666 !default;
$colour-sidebar-links: black !default;
$colour-toolbar: $colour-oira-cyan !default;
$colour-lines: #e6e6e6 !default;
$colour-menu-lines: #c8c8c8 !default; 
$colour-tree-section-active: #efefef !default;
$colour-form-fields-background: rgba(0,0,0,0.04) !default;

$colour-ueber-back-button: #d7d7d7 !default;
$colour-ueber-back-button-text: $colour-link !default;

$colour-button-continue: $colour-primary !default;
$colour-button-continue-text: white !default;

$colour-separation-lines: #dddddd !default;

$border-radii: 10px !default;

$button-border: none !default;
$button-border-radius: $border-radii !default;
$button-background-image: none !default;
$button-font-weight: bold !default;

$button-background-colour: rgba(0, 0, 0, 0.15) !default;
$button-text-colour: rgba(0,0,0,0.6) !default;
$button-text-colour-hover: black !default;

$button-background-colour-subdued: rgba(0, 0, 0, 0.15) !default;
$button-text-colour-subdued: rgba(0,0,0,0.6) !default;
$button-text-colour-subdued-hover: black !default;

$button-background-colour-default: $colour-primary !default;
$button-background-colour-default-hover: $colour-primary !default;
$button-text-colour-default: white !default;
$button-text-colour-default-hover: white !default;

$back-button-background-colour: #d9d9d9 !default;
$back-button-text-colour: #575757 !default;
$back-button-text-colour-hover: black !default;

// Shadows

$default-box-shadow: 0 0 3px rgba(0, 0, 0, 0.25) !default;
$default-box-shadow-hover: 0 0 15px rgba(0,0,0,0.2) !default;
$default-box-shadow-active: inset 0 0 10px rgba(0,0,0,0.4) !default;

// Photoswipe 
$pswp__assets-path: "/style/";

// Form parameters

$form-param-is: "is";
$form-param-equals: "equals";
$form-param-not-equals: "";
$form-param-contains: "";
$form-param-contains-not: "";
$form-param-before: "";
$form-param-after: "";

