.ra-list {
	margin-bottom: 2rem;

	.ra-list-title {
		font-size: 1.2em;
		margin-bottom: 0.5em;
	}

	.tool-name {
		display: block;
		padding-left: 1rem;
		font-size: 1em;
		padding-top: 0.2em;
		padding-bottom: 0.2em;

		&:before {
			font-family: fontello;
			content: '#{$glyph-tool}';
			display: inline-block;
			margin-right: 0.5em;
		}
	}

	.department {
		padding-left: 1rem;
	}
}
	
ul.ras {
	margin-left: 0;
	margin-bottom: 0.7em;
	display: table;
	width: 100%;

	.ra {
		
		line-height: 1.4;
		padding-right: 60px;
		position: relative;
		display: table-row;

		&.archived {
			.field {
				&.name {				
					&:after {
						// position: absolute;
						// right: 4px;
						// top: 50%;
						// transform: translateY(-50%);
						font-family: fontello;
						content: '#{$glyph-archived}';
						// font-size: 14px;
						font-size: 0.6em;
						vertical-align: middle;
						padding-left: 0.5em;
						position: relative;
						top: -0.1em;
						// color: gray;
					}
				}
			}
		}



		.field {
			padding-top: 0.2em;
			padding-bottom: 0.2em;
			display: table-cell;

			@include screen-size(medium-large) {
				display: table-cell;
				vertical-align: middle;
				padding-left: 0.5rem;
				padding-right: 0.5rem;
			}

			.span-1 & {
				display: block;
				padding: 0;
			}

			.last-saved {
				clear: both;
			}

			&.name,
			&.tool {

				a {
					padding-left: 1.5rem;
					display: block;

					&:before {
						font-family: fontello;
						display: inline-block;
						// margin-right: 0.5em;
						margin-left: -1.5rem;
						float: left;
						margin-top: -0.15em;
					}

					&:hover {
						text-decoration: underline;
					}
				}
			}

			&.name {
				padding-left: 0;
				a {
					&:before {
						content: '#{$glyph-risk-assessment}';
					}
				}
			}

			&.tool {
				a {
					&:before {
						content: '#{$glyph-tool}';
					}
				}
			}

			&.icons {
				width: 0;
				text-align: right;
				white-space: nowrap;
				padding-left: 20px;
				padding-right: 0;

				.icon {
					display: inline-block;
					margin-left: 20px;
					vertical-align: middle;
					width: 22px;
					height: 22px;
					line-height: 22px;
				}
			}

			&.date,
			&.status {
				color: gray;

				.span-1 & {
					padding-left: 1.5rem;
				}

				@include screen-size(small) {
					padding-left: 1.5rem;
				}

				@include screen-size(medium-large) {
					text-align: right;
					white-space: nowrap;

					.span-1 & {
						text-align: left;
						white-space: inherit;
						padding-bottom: 0.4em;
					}

					.published {
						background: #efefef;
						border-radius: 4px;
						display: inline-block;
						padding: 0 0.4em;
						margin-right: 15px;

						.span-1 & {
							background: transparent;
							display: inline;
							padding: 0;
							margin: 0;
						}
					}
				}
			}
		}


		
	}
}
