@charset "UTF-8";

.pat-progress-bar {
  height: 10px;
  border-radius: 10px;
  border: 2px solid $colour-accent;
  display: block;
  overflow: hidden;
  background-color: white;
  margin-right: 1em;

  .progress-bar-segment {
    background-color: $colour-accent;
    height: 10px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    color: transparent;
    float: left;
  }
}