@charset "UTF-8";

$pat-toolbar-button-height: $button-height !default;
$colour-toolbar-button: transparent !default;
$pat-toolbar-element-separation: 10px !default;
$pat-tail-white: 15px !default;
$pat-toolbar-font-size: 18px !default;

.pat-toolbar {
	@include clear-fix();
	color: rgba(0,0,0,0.6);
	position: relative;
	z-index: 2;
	height: $pat-toolbar-button-height;
	padding: 10px;
	margin: 0 -10px 40px;
	border-radius: $border-radii;
	font-size: $pat-toolbar-font-size;

	&.inline {
		margin: 0;
		padding: 0;
	}

	&.sticky {
		-webkit-position: sticky;
		position: sticky;
		transition: box-shadow 1s;
		top: -40px;
		transition: all 1s;

	    #content:not(.scroll-position-top) &,
	    #content:not(.scroll-position-top) & {
	      	box-shadow: $default-box-shadow;

			background-color: rgba(255,255,255,0.9);
			.backdropfilter & {
				background-color: rgba(255,255,255,0.15);
				-webkit-backdrop-filter: blur(50px) saturate(4) brightness(1.2);
				backdrop-filter: blur(50px) saturate(4) brightness(1.2);
			}
	    }
	}

	.toolbar-navigation-area,
	.toolbar-functions-area {
		display: grid;
		grid-template-columns: 1fr 4fr 1fr;
		grid-template-areas: "left middle right";
	}

	.toolbar-navigation-area {
		height: 0;
	}

	.toolbar-section {
		vertical-align: top;
		box-sizing: border-box;

		* {
			white-space: normal;
		}

		&.view-options {
			grid-area: left;

			.pat-button-cluster,
			.pat-buttton,
			> .icon {
				margin-right: 30px;
			}

			@include screen-size(small) {
				float: left;
				width: auto;			}
		}

		&.quick-functions {
			text-align: right;
			grid-area: right;

			.icon {
				margin-left: 20px;
			}
		}

		&.navigation {
			grid-area: middle;

			@include screen-size(medium-large) {
				text-align: center;
			}

			.pat-select {
				margin: 0;
				box-shadow: inset 0 1px 6px rgba(0,0,0,0.15);
			}
		}
	}
			
	.toolbar-subnav {
		position: relative;
		height: $button-height;
		line-height: $button-height;
		border-radius: $border-radii;
		overflow: visible;
		float: none;
		white-space: nowrap;
		max-width: 100%;
		font-size: 0;
	    border: none;
	    display: inline-block;
	    vertical-align: top;

		&.align-centre {
			position: absolute;
		}
	
		&.pat-tabs {
			padding-right: 60px;
		}

		a {
			float: none;
			display: inline-block;
			font-size: $base-font-size;
			vertical-align: top;

			&.icon {
				width: 14px;
				overflow: hidden;
				padding: 0 20px;

				color: transparent;

				&:before {
					margin-right: 20px;
				}
			}
		}

		&.open {
			span.extra-tabs {
				overflow: visible;

				&:before {
				  	color: black;
		    		background-color: rgba(255,255,255,0.4);
				}

				&:after {
					border-color: silver;
				}

				height: auto;
				width: 300px;
				padding-bottom: 15px;
				padding-top: $button-height + 25px;

				a {
					animation: fadeIn 0.5s;
					animation-delay: 0.2s;
					animation-fill-mode: forwards;
				}
			}
		}

		& {

			span.extra-tabs {
				font-size: $base-font-size;
			    width: 300px;
				padding-top: $button-height + 10px;
				display: inline-block;
				float: right;
				height: 0;
				width: $button-height + 20px;
				overflow: hidden;
				position: absolute;
				right: 0;
				z-index: 1;
				background: transparent !important;
				box-shadow: none !important;
				box-sizing: border-box;

				-webkit-transition: all 0.1s;

				&:before {
					font-family: fontello;
					content: '\e87b';
					display: block;
					height: $button-height;
					line-height: $button-height + 1px;
					width: $button-height + 20px;
					text-align: center;
					position: absolute;
					top: 0;
					right: 0;
					border-top-right-radius: $border-radii;
					border-bottom-right-radius: $border-radii;
				}

				&:after {
					content: ' ';
					display: block;
					position: absolute;
					top: $button-height + 10px;
					right: 0;
					bottom: 0;
					left: 0;
				    border: 1px solid rgba(0,0,0,0);
				    box-shadow: 0 2px 15px rgba(0, 0, 0, 0.2);
				    border-radius: $border-radii;
				    background: white;

				    transition: border 0.2s;
				}

				&:hover {
			    	&:before {
			    		background-color: rgba(255,255,255,0.4);
			    		color: black;
			    	}
				}

				a {
					display: block;
					float: none;
					padding: 0.4em 0.7em;
					line-height: 1.5;
					text-align: left;
					color: black;
					position: relative;
					z-index: 1;
					border: none !important;
					opacity: 0;
					border-radius: 0 !important;

					&:hover {
					  background-color: rgba(0,0,0,0.05);
					  text-decoration: none;
					}

					&.current {
						background: $colour-accent;
						color: white;
					}
				}
			}
		}
	}

	@media print { display: none; }

	> label.pat-checklist {
		margin-right: 1em;
		
		&:hover {
			color: black;
		}
	}

	.pat-context-menu {
		display: inline-block;
	}

	> .pat-select,
	.toolbar-section > .pat-select,
	.pat-context-menu > .context-menu-label,
	> input[type="search"] {
		display: inline-block;
		vertical-align: top;
		height:  $canvas-toolbar-button-height;
		line-height: $canvas-toolbar-button-height;

		select {
			line-height: $pat-toolbar-button-height;
			padding-top: 0;
			padding-bottom: 0;
		}
	}
	
	.pat-button,
	.icon {
		display: inline-block;
		vertical-align: top;
		line-height: $pat-toolbar-button-height;
		border: none;
		background-color: $colour-toolbar-button;
		float: none;

		&.meta-data-toggle {
			transition: transform 0.3s;

			.more-metadata & {
				transform: scaleY(-1);
			}
		}

		@include screen-size(small) {
			margin: 0 20px 0 0;
		}

		&:active {
			box-shadow: inset 0 1px 3px rgba(0,0,0,0.15);
		}

		&:not(.active):not([disabled]):hover {
			color: black
		}
	}

	>,
	.pat-toolbar-section > {
		.pat-button,
		.pat-icon,
		.pat-button-cluster,
		.pat-back-button,
		.pat-select,
		.pat-context-menu {
			margin-right: $pat-toolbar-element-separation;
		}
	}

	.pat-progress-bar {
		margin-top: 9px;
		width: 540px;
		float: left;
	}

	.icon {
		font-family: fontello;
		text-indent: -1000em;
		width: $canvas-toolbar-button-height;
		height: $canvas-toolbar-button-height;
		position: relative;
		color: rgba(0,0,0,0.6);
		cursor: default;
		line-height: $canvas-toolbar-button-height;
		// float: left;
		border-radius: $button-border-radius;
		display: inline-block;
		text-align: left;

		&.progress {
			.pie-graph {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
			}

			&.pat-tooltip.tooltip-active-click {
				background-color: $colour-accent;

				.pie-graph {
					border-color: white;;
				}
			}
		}

		&:before {
			font-family: fontello;
			text-indent: 0;
			line-height: $canvas-toolbar-button-height + 1px;
			text-align: center;
			position: absolute;
			margin: 0;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			display: inline-block;
			border-radius: $button-border-radius;
			box-sizing: border-box;
		}

		&:hover {
			color: black;
			text-decoration: none;

			&:before {
				// border: black 1px solid;
			}
		}

		&.active {
			color: $colour-accent;
	    	box-shadow: none;

			&:before {
				color: $colour-accent;
			}
		}

		&.pat-tooltip.tooltip-active-click {


			&:before {
				background-color: $colour-accent;
				color: white;
			}
		}

		&.create {
			// float: right;

			&:before {
				content: "#{$glyph-plus}";
				font-size: 25px;
			}
		}

		&.edit {
			// float: right;

			&:before {
				content: "#{$glyph-icon-edit}";
			}
		}

		&.modify {
			// float: right;

			&:before {
				content: "#{$glyph-icon-cog}";
			}
		}

		&.more {
			// float: right;

			&:before {
				content: "#{$glyph-icon-ellipsis}";
			}
		}

	}

	.pat-toolbar-search-field {
		width: $pat-toolbar-button-height;
		height: $pat-toolbar-button-height;
		position: relative;
		// float: left;
		display: inline-block;
		z-index: 1;
		margin-left: 1em;
		vertical-align: top;

		&:after {
			position: absolute;
			top: 0;
			left: 0;
			line-height: $pat-toolbar-button-height;
			width: $pat-toolbar-button-height;
			text-align: center;
			font-family: fontello;
			content: '#{$glyph-icon-search}';
			font-size: 22px;
			transition: left 0.5s, padding-left 0.5s, transform 1s;
		}

		&:hover {
			color: black;
		}

		input {
			width: $pat-toolbar-button-height;
			height: $pat-toolbar-button-height;
			border-radius: $border-radii;
			background: white;
			border: 1px silver solid;
			max-width: 1000px;
			position: absolute;
			top: 0;
			right: 0;
			transition: all 0.5s;
			opacity: 0;
			padding-left: 34px;
			margin: 0;

			&:focus {
				outline: none;
				box-shadow: none;
			}
		}

		&.focus,
		&.has-value,
		&.fixed {
			input {
				width: 270px;
				opacity: 1;

				@include screen-size(medium-large) {
					width: 350px;
				}
			}

			&:after {
				color: gray;
				transform: scale(0.8);
				left: -270px;

				@include screen-size(medium-large) {
					left: -350px;
					padding-left: 40px;
				}
			}
		}

		&.fixed {
			width: 258px;

			&:after {
				left: -123px;
			}
		}
	}
}